.profile__wrapper {
  position: relative;
  padding: 0 25px;
  padding-top: 20px;
  padding-bottom: 34px;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-areas: "level photo" "button button" "tags tags" "transport transport";
  column-gap: 35px;
}
.profile__level {
  grid-area: level;
  margin-left: auto;
  width: 100px;
  height: 100px;
}
.profile__level .level__progress-bar {
  width: 100px;
  height: 100px;
}
.profile__level .level__progress-bar-scale {
  fill: none;
}
.profile__level .level__progress-bar-indicator {
  stroke-width: 3px;
  stroke-linecap: square;
}
.profile__level .level__inner-wrapper {
  left: 30px;
  top: 20px;
  text-align: center;
  color: #161C35;
}
.profile__level .level__inner-wrapper:before {
  top: 37px;
  width: 50px;
  background-color: #444444;
}
.profile__level .level__number {
  font-size: 38px;
  line-height: 38px;
  margin-bottom: 6px;
}
.profile__level .level__text {
  font-size: 18px;
  line-height: 18px;
}
.profile__photo-box {
  grid-area: photo;
  width: 100px;
  height: 100px;
}
.profile__photo {
  border-radius: 50%;
}
.profile__button {
  grid-area: button;
  margin-top: 18px;
  margin-bottom: 33px;
  padding: 12px 0;
  font-size: 20px;
  line-height: 18px;
  font-weight: 500;
  text-transform: uppercase;
  color: #161c35;
  background-color: #dfe3f0;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}
.profile__hashtags-box {
  grid-area: tags;
  position: relative;
  margin-bottom: 25px;
}
.profile__hashtags-title,
.profile__transport-title {
  position: absolute;
  top: -7px;
  z-index: 1000;
  margin: 0;
  font-size: 16px;
  line-height: 18px;
  text-align: center;
  text-transform: uppercase;
  background-color: #EDEFF6;
}
.profile__hashtags-title {
  width: 70px;
  left: calc(50% - 35px);
}
.profile__hashtags {
  margin: 0;
  padding: 27px 24px;
  opacity: 0.6;
  border: 1px solid #CBCED9;
  border-radius: 10px;
}
.profile__transport-box {
  grid-area: transport;
  position: relative;
}
.profile__transport-title {
  width: 110px;
  left: calc(50% - 55px);
}
.profile__transport-list {
  margin: 0;
  padding: 0;
  padding-top: 16px;
  padding-bottom: 15px;
  border: 1px solid #CBCED9;
  border-radius: 10px;
  list-style: none;
  display: flex;
  justify-content: center;
}
.profile__transport-item {
  display: flex;
  width: 32px;
  height: 32px;
  background-color: #fff;
  border-radius: 50%;
}
.profile__transport-icon {
  margin: auto;
}
